<template>
  <div class="login-wrap">
    <div class="top">
      <img src="../assets/logo.png">
      <p>{{ msg }}</p>
    </div>
    <form class="bot">
      <div class="cell">
        <img src="../assets/userico.png">
        <input type="text" placeholder="请输入用户名" v-model="username" required>
      </div>
      <div class="cell">
          <img src="../assets/pasdico.png">
          <input type="password" placeholder="请输入密码" v-model="password">
      </div>
      <div class="btn">
          <button v-on:click="loginE">登录</button>
      </div>
    </form>
  </div>
</template>

<script>
  import navjs from '../router/nav.js'

  export default {
    name: 'Login',
    data() {
      return {
        msg: '济宁市重点用能单位能耗在线监测系统',
        username: '',
        password: ''
      }
    },
    methods: {
      loginE () {
        this.$router.push("/index")
        navjs.$emit('val', true)
      }
    }
    // mounted() {
    //   this.windowH = window.screen.height;
    //   console.log(this.windowH)
    // },

  }
</script>

<style lang="less" scoped>
  .login-wrap {
    width: 100vw;
    height: 100vh;
    background: #061d35 url(../assets/loginbg.png) center bottom no-repeat;
    padding-top: 2rem;
  }
  .top {
    text-align: center;
    img {
      width: 2rem;
      height: 2rem;
    }
    p{
      font-size: .4rem;
      color: #fff;
      margin-top: .666667rem;
    }
  }
  .bot {
    margin-top: 1.733333rem;
    .cell {
      width: 6.666667rem;
      margin: auto;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #fff;
      padding-bottom: .173333rem;
      margin-bottom: .466667rem;
      img {
        width: .666667rem;
        margin-right: .6rem;
      }
      input {
        color: #fff;
      }
    }
  }
  .btn {
    margin-top: .733333rem;
    text-align: center;
    button {
      width: 6.666667rem;
      height: 1.066667rem;
      background: #164a64;
      color: #fff;
      border-radius: 4px;
      letter-spacing: .333333rem;
    }
  }
</style>



