import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'

Vue.use(Router)

export default new Router({
  routes: [
    //默认
    {
      path: '/',
      redirect: 'login'
    },
    //登录
    {
      path: '/login',
      name: 'Login',
      component: Login
      //component: () => import('@/components/Login')
    },
    //首页
    {
      path: '/index',
      name: 'indexLink',
      component: () => import('@/components/index')
    },
    //数据质量
    {
      path: '/dataQuality',
      name: 'dataQLink',
      component: () => import('@/components/dataQuality')
    },
    //能效分析
    {
      path: '/efficiencyAnalysis',
      name: 'efficiencyLink',
      component: () => import('@/components/efficiency')
    },
    //节能管理
    {
      path: '/energyManagement',
      name: 'manaLink',
      component: () => import('@/components/management')
    },
    //我的
    {
      path: '/mine',
      name: 'mineLink',
      component: () => import('@/components/mine')
    }

  ],
  //mode: 'history'
})





