<template>
  <nav>
    <router-link tag="div" to="/index" class="cell" v-bind:class="$route.path == '/index'?on: ''">
      <img src="../../assets/nav/page12.png" v-if="$route.path == '/index'?true: false">
      <img src="../../assets/nav/page1.png" v-else>
      <p>首页</p>
    </router-link>
    <router-link tag="div" to="/dataQuality" class="cell" v-bind:class="$route.path == '/dataQuality'?on: ''">
      <img src="../../assets/nav/page22.png" v-if="$route.path == '/dataQuality'?true: false">
      <img src="../../assets/nav/page2.png" v-else>
      <p>数据质量</p>
    </router-link>
    <router-link tag="div" to="/efficiencyAnalysis" class="cell" v-bind:class="$route.path == '/efficiencyAnalysis'?on: ''">
      <img src="../../assets/nav/page32.png" v-if="$route.path == '/efficiencyAnalysis'?true: false">
      <img src="../../assets/nav/page3.png" v-else>
      <p>能效分析</p>
    </router-link>
    <router-link tag="div" to="/energyManagement" class="cell" v-bind:class="$route.path == '/energyManagement'?on: ''">
      <img src="../../assets/nav/page42.png" v-if="$route.path == '/energyManagement'?true: false">
      <img src="../../assets/nav/page4.png" v-else>
      <p>节能管理</p>
    </router-link>
    <router-link tag="div" to="/mine" class="cell" v-bind:class="$route.path == '/mine'?on: ''">
      <img src="../../assets/nav/page52.png" v-if="$route.path == '/mine'?true: false">
      <img src="../../assets/nav/page5.png" v-else>
      <p>我的</p>
    </router-link>
  </nav>
</template>

<script>
  export default {
    name: 'Nav',
    data() {
      return {
        on: 'on'
      }
    }
  }
</script>

<style lang="less" scoped>
  nav {
    display: flex;
    align-items: flex-end;
    height: 1.466667rem;
    padding-bottom: .133333rem;
    background: #061d35;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    .cell {
      width: 20%;
      text-align: center;
      img {
        width: 30%;
      }
      p {
        font-size: .293333rem;
      }
    }
    .cell.on {
      p {
        color: #2dded4;
      }
    }
  }
</style>



