<template>
  <div id="app">
    <router-view/>
    <Nav v-if="navShow"></Nav>
  </div>
</template>

<script>
import Nav from './components/part/Nav'
import navjs from './router/nav.js'

export default {
  name: 'App',
  data() {
    return {
      navShow: true,
    }
  },
  components: {
    Nav,
  },
  created() {
    this.showNav()
    //console.log(this.$route.name)
  },
  mounted() {
    navjs.$on('val', (data) => {
      //console.log(data)
      this.navShow = data
    })
  },
  methods:{
    showNav() {
      if(this.$route.name == 'Login') {
        this.navShow = false
      }
    },
    changeNavShow(val) {
      this.navShow = val
      //console.log(this.navShow)
    }
  },
}
</script>

<style>
  @import './assets/fonts/font.css';

  * {
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    border: none;
    background: none;
    color: #fff;
    font-family: 'Avenir', Helvetica, Arial, sans-serif, '微软雅黑';
    box-sizing: border-box;
    outline: none;
  }
#app {
  width: 100vw;
  min-height: 100vh;
  background: #061d35;
}
.wrap-nav {
  padding-bottom: 1.466667rem;
  background: #061d35;
}
</style>



